/* eslint no-useless-escape: 0 */

import React from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { isEmpty } from "lodash";
import { objectOf, func, any } from "prop-types";

import {
    galleryDetailContainer,
    galleryDetailWrapperGallery,
    galleryDetailWrapperNavigator
} from "./styles";
import GalleryDetail from "../GalleryDetail";

const GalleryDetailWrapper = ({ designs, match, onClickNext, onClickPrev }) => {
    const re = /^\/([^\/]*).*$/;
    const homeURL = match.path.replace(re, "$1");

    return (
        <div className={galleryDetailContainer}>
            <div className={galleryDetailWrapperGallery}>
                {!isEmpty(designs.data) && <GalleryDetail designs={designs.data} />}
            </div>
            <div className={galleryDetailWrapperNavigator}>
                <div
                    className={`${galleryDetailWrapperNavigator}Left`}
                    onClick={onClickPrev}
                    role="presentation"
                >
                    <FontAwesomeIcon icon="chevron-left" />
                </div>
                <Link to={`/${homeURL}`}>
                    <div className={`${galleryDetailWrapperNavigator}Home`}>
                        <FontAwesomeIcon icon="home" />
                    </div>
                </Link>
                <div
                    className={`${galleryDetailWrapperNavigator}Right`}
                    onClick={onClickNext}
                    role="presentation"
                >
                    <FontAwesomeIcon icon="chevron-right" />
                </div>
            </div>
        </div>
    );
};

GalleryDetailWrapper.propTypes = {
    designs: objectOf(any).isRequired,
    match: objectOf(any).isRequired,
    onClickNext: func.isRequired,
    onClickPrev: func.isRequired
};

export default GalleryDetailWrapper;
