import React, { Component } from "react";
import { objectOf, string } from "prop-types";

import { authWrapper, authForm } from "./styles";
import Form from "../../components/Common/Form";
import Input from "../../components/Common/Input";
import Button from "../../components/Common/Button";
import { formValidation, submitValidation } from "../../helpers/form-validation";
import Alert from "../../components/Alert";
import { post } from "../../api";

class Register extends Component {
    state = {
        form: {
            username: "",
            password: "",
            confirmPassword: "",
            name: "",
            email: ""
        },
        formErrors: {
            username: "",
            password: "",
            confirmPassword: "",
            name: "",
            email: ""
        },
        formValidity: {
            username: "",
            password: "",
            confirmPassword: "",
            name: "",
            email: ""
        },
        isSubmitting: false
    };

    handleSubmit = e => {
        e.preventDefault();

        const { form, isSubmitting } = this.state;
        const { username, password, confirmPassword, name, email } = form;

        this.setState({
            isSubmitting: !isSubmitting
        });

        if (password !== confirmPassword) {
            Alert("error", "Password dan konfirmasi password tidak sesuai");
            this.setState({
                isSubmitting: false
            });

            return;
        }

        post("/users/create", {
            username,
            password,
            confirm_password: confirmPassword,
            name,
            email,
            role: "super"
        })
            .then(data => {
                if (data.status.success) {
                    const { history } = this.props;

                    Alert("success", "Akun baru berhasil ditambahkan");
                    history.push("/auth");
                }

                Alert("error", data.message);

                this.setState({
                    isSubmitting: false
                });

                return null;
            })
            .catch(error => {
                Alert("error", `Server Error: ${error}`);
                console.error(`Server Error: ${error}`);

                this.setState({
                    isSubmitting: false
                });
            });
    };

    handleChangeInput = e => {
        e.preventDefault();

        const { value, name } = e.target;
        const { formErrors, formValidity, form } = this.state;
        const { validationError, validity } = formValidation(name, value, formValidity, formErrors);

        this.setState(
            prevState => ({
                form: {
                    ...prevState.form,
                    [name]: value
                },
                formErrors: validationError,
                formValidity: validity
            }),
            () => {
                this.setState({ canSubmit: submitValidation(formValidity, form) });
            }
        );
    };

    render() {
        const { form, formErrors, isSubmitting, canSubmit } = this.state;
        const { username, password, confirmPassword, name, email } = form;
        const {
            username: usernameError,
            password: passwordError,
            confirmPassword: confirmPasswordError,
            name: nameError,
            email: emailError
        } = formErrors;

        return (
            <div className={authWrapper}>
                <Form className={authForm} onSubmit={this.handleSubmit}>
                    <label>Username:</label>
                    <Input
                        onChange={this.handleChangeInput}
                        value={username}
                        name="username"
                        type="text"
                        id="username"
                        errorMsg={usernameError}
                    />
                    <label>Name:</label>
                    <Input
                        onChange={this.handleChangeInput}
                        value={name}
                        name="name"
                        type="text"
                        id="name"
                        errorMsg={nameError}
                    />
                    <label> Email:</label>
                    <Input
                        onChange={this.handleChangeInput}
                        value={email}
                        name="email"
                        type="email"
                        id="email"
                        errorMsg={emailError}
                    />
                    <label>Password: </label>
                    <Input
                        onChange={this.handleChangeInput}
                        value={confirmPassword}
                        name="confirmPassword"
                        type="password"
                        id="confirmPassword"
                        errorMsg={passwordError}
                    />
                    <label>Confirm Password: </label>
                    <Input
                        onChange={this.handleChangeInput}
                        value={password}
                        name="password"
                        type="password"
                        id="password"
                        errorMsg={confirmPasswordError}
                    />
                    <Button type="submit" classBtn="submit" disabled={!canSubmit}>
                        {isSubmitting ? "SUBMITTING..." : "SIGN IN"}
                    </Button>
                </Form>
            </div>
        );
    }
}

Register.propTypes = {
    history: objectOf(string).isRequired
};

export default Register;
