import React from "react";
import ReactTable from "react-table";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import PropTypes from "prop-types";
import { noop } from "lodash";

import ImageLoader from "../../ImageLoader";
import {
    settingsColumns as settingsColumnsStyle,
    settingsImageReview,
    settingsActionColumns,
    settingsActionDelete,
    settingsActionBg
} from "./styles";

const settingsColumns = (onChangeBackground, onDeleteDesign, onEditDesign, idBackground) => [
    {
        Header: "Gallery Configurations",
        columns: [
            {
                Header: "Preview",
                id: "preview",
                Cell: row => (
                    <div className={settingsImageReview}>
                        <ImageLoader
                            srcLoaded={row.original.images[0] && row.original.images[0].url}
                        />
                    </div>
                ),
                width: 100
            },
            {
                Header: "Title",
                id: "title",
                Cell: row => <div className={settingsColumnsStyle}>{row.original.title}</div>,
                style: { whiteSpace: "unset", lineHeight: "1.5" }
            },
            {
                Header: "Category",
                id: "category",
                Cell: row => (
                    <div className={settingsColumnsStyle}>
                        {row.original.category && row.original.category.name}
                    </div>
                ),
                width: 120
            },
            {
                Header: "",
                id: "action",
                Cell: row => (
                    <div className={settingsColumnsStyle}>
                        <div className={settingsActionColumns}>
                            <div
                                role="presentation"
                                onClick={() => onEditDesign(row.original.id)}
                                className={settingsActionDelete}
                            >
                                <FontAwesomeIcon icon="edit" size="1x" />
                            </div>
                            <div
                                role="presentation"
                                onClick={() => onDeleteDesign(row.original.id)}
                                className={settingsActionDelete}
                            >
                                <FontAwesomeIcon icon="trash" size="1x" />
                            </div>
                            <div
                                onClick={() => onChangeBackground(row.original.id)}
                                role="presentation"
                                className={settingsActionBg}
                            >
                                <FontAwesomeIcon
                                    icon="images"
                                    size="1x"
                                    style={{
                                        color: row.original.id === idBackground ? "#4caf50" : "#333"
                                    }}
                                />
                            </div>
                        </div>
                    </div>
                ),
                width: 100
            }
        ]
    }
];

const Table = ({ data, onChangeBackground, onDeleteDesign, onEditDesign, idBackground, type }) => (
    <ReactTable
        data={data}
        noDataText="No data found"
        columns={
            type === "settings"
                ? settingsColumns(onChangeBackground, onDeleteDesign, onEditDesign, idBackground)
                : null
        }
        defaultPageSize={10}
        className="-highlight"
        resizable={false}
        sortable={false}
    />
);

Table.propTypes = {
    data: PropTypes.arrayOf(PropTypes.any),
    onChangeBackground: PropTypes.func,
    onDeleteDesign: PropTypes.func,
    onEditDesign: PropTypes.func,
    idBackground: PropTypes.number,
    type: PropTypes.string.isRequired
};

Table.defaultProps = {
    data: [],
    onChangeBackground: noop,
    onDeleteDesign: noop,
    onEditDesign: noop,
    idBackground: null
};

export default Table;
