import React, { Component, createRef } from "react";
import { string } from "prop-types";

import {
    imageContainer,
    imageLoaded,
    imageFadeIn,
    imageSpinnerHidden,
    imageSpinner
} from "./styles";
import Placeholder from "../../assets/images/placeholder.png";
import BrokenPlaceholder from "../../assets/images/placeholder-broken.jpeg";

class ImageLoader extends Component {
    constructor(props) {
        super(props);
        this.imageRef = createRef();
        this.loader = null;
    }

    componentDidMount() {
        const { srcLoaded } = this.props;
        const tempImage = new Image();
        tempImage.src = srcLoaded;

        tempImage.onload = () => {
            if (this.imageRef.current) {
                this.imageRef.current.setAttribute("src", srcLoaded);

                this.imageRef.current.setAttribute("style", "display:block");

                this.imageRef.current.classList.add(imageFadeIn);
                this.loader.classList.add(imageSpinnerHidden);
            }
        };

        tempImage.onerror = () => {
            if (this.imageRef.current) {
                this.imageRef.current.setAttribute("src", BrokenPlaceholder);

                this.imageRef.current.setAttribute("style", "display:block");

                this.imageRef.current.classList.add(imageFadeIn);
                this.loader.classList.add(imageSpinnerHidden);
            }
        };
    }

    componentDidUpdate(prevProps) {
        const { srcLoaded } = this.props;
        const tempImage = new Image();
        tempImage.src = srcLoaded;

        if (prevProps.srcLoaded !== srcLoaded) {
            tempImage.src = srcLoaded;

            tempImage.onload = () => {
                if (this.imageRef.current) {
                    this.imageRef.current.setAttribute("src", srcLoaded);

                    this.imageRef.current.setAttribute("style", "display:block");

                    this.imageRef.current.classList.add(imageFadeIn);
                    this.loader.classList.add(imageSpinnerHidden);
                }
            };

            tempImage.onerror = () => {
                if (this.imageRef.current) {
                    this.imageRef.current.setAttribute("src", BrokenPlaceholder);

                    this.imageRef.current.setAttribute("style", "display:block");

                    this.imageRef.current.classList.add(imageFadeIn);
                    this.loader.classList.add(imageSpinnerHidden);
                }
            };
        }
    }

    render() {
        const { className } = this.props;
        return (
            <div className={[imageContainer, className].join(" ")}>
                <img
                    className={imageLoaded}
                    ref={this.imageRef}
                    alt="displayed img"
                    src={Placeholder}
                />
                <div
                    className={imageSpinner}
                    ref={spinnerLoadedElem => {
                        this.loader = spinnerLoadedElem;
                    }}
                >
                    <div className="rect1" />
                    <div className="rect2" />
                    <div className="rect3" />
                    <div className="rect4" />
                    <div className="rect5" />
                </div>
            </div>
        );
    }
}

ImageLoader.propTypes = {
    srcLoaded: string,
    className: string
};

ImageLoader.defaultProps = {
    srcLoaded: "",
    className: ""
};

export default ImageLoader;
