import { css, keyframes } from "react-emotion";

export const imageContainer = css`
    position: relative;
    width: 100%;
    height: auto;
    overflow: hidden;
    display: block;
`;

export const imageLoaded = css`
    width: 100%;
    height: 100%;
    display: none;
    object-fit: cover;
    opacity: 0;
    transition: opacity 1s ease;
`;

export const imageFadeIn = css`
    opacity: 1;
`;

const animationFrame = keyframes`
    0%, 40%, 100% { 
        transform: scaleY(0.4);
        -webkit-transform: scaleY(0.4);
    }  
    
    20% { 
        transform: scaleY(1.0);
        -webkit-transform: scaleY(1.0);
    }
`;

export const imageSpinner = css`
    margin: 100px auto;
    width: 30px;
    height: 60px;
    border: none;
    text-align: center;
    font-size: 10px;
    display: inline-block;

    > div {
        background-color: #ddd;
        height: 100%;
        width: 6px;
        display: inline-block;
        -webkit-animation: ${animationFrame} 1.2s infinite ease-in-out;
        animation: ${animationFrame} 1.2s infinite ease-in-out;
    }

    .rect2 {
        -webkit-animation-delay: -1.1s;
        animation-delay: -1.1s;
    }

    .rect3 {
        -webkit-animation-delay: -1s;
        animation-delay: -1s;
    }

    .rect4 {
        -webkit-animation-delay: -0.9s;
        animation-delay: -0.9s;
    }

    .rect5 {
        -webkit-animation-delay: -0.8s;
        animation-delay: -0.8s;
    }
`;

export const imageSpinnerHidden = css`
    display: none;
`;
