import React, { Component } from "react";
import queryString from "query-string";
import { objectOf, string } from "prop-types";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Turndown from "turndown";
import * as Showdown from "showdown";
import ReactMde from "react-mde";

import { get, patch } from "../../../api";
import { formValidation, submitValidation, errorClass } from "../../../helpers/form-validation";
import Alert from "../../../components/Alert/index";
import Button from "../../../components/Common/Button";
import Form from "../../../components/Common/Form";
import Input from "../../../components/Common/Input";
import Select from "../../../components/Common/Select";
import {
    settingsEditImageWrapper,
    settingsEditAddImagesIcon,
    settingsEditRemoveImagesIcon,
    settingsEditTextEditor,
    settingsEditContainer
} from "./styles";

class SettingEdit extends Component {
    constructor() {
        super();
        this.converter = new Turndown();
        this.converterMarkdown = new Showdown.Converter({
            tables: true,
            simplifiedAutoLink: true,
            strikethrough: true,
            tasklists: true
        });
    }

    state = {
        imageUrlInput: [""],
        form: {
            title: "",
            category: "1",
            imageUrl: [""],
            desc: ""
        },
        formErrors: {
            title: "",
            category: "",
            imageUrl: [""]
        },
        formValidity: {
            title: false,
            category: false,
            imageUrl: [false]
        },
        tab: "write"
    };

    componentDidMount() {
        const { location } = this.props;
        const { postId } = queryString.parse(location.search);

        get(`/posts/get/${postId}`)
            .then(response => {
                if (response.status.success) {
                    this.setState({
                        imageUrlInput: !response.data.images.length
                            ? [""]
                            : response.data.images.map(() => ""),
                        form: {
                            title: response.data.title,
                            category: response.data.categoryId,
                            imageUrl: !response.data.images.length
                                ? [""]
                                : response.data.images.map(image => image),
                            desc: this.converter.turndown(response.data.description)
                        }
                    });
                } else {
                    Alert("error", "Server Error: Getting Spesific Post: ", response.data.message);
                }
            })
            .catch(error => {
                Alert("error", "Internal Server Error: Getting Spesific Post: ", error);
            });

        get("/categories/get/all").then(data => {
            this.setState({
                categories: data.data
            });
        });
    }

    handleSubmitValidation = () => {
        const { formValidity, form } = this.state;
        this.setState({ canSubmit: submitValidation(form, formValidity) });
    };

    handleFormSubmit = e => {
        e.preventDefault();
        const { form } = this.state;
        const { location } = this.props;
        const { postId } = queryString.parse(location.search);

        const submittedForm = {
            id: postId,
            title: form.title,
            description: this.converterMarkdown.makeHtml(form.desc),
            images: [...form.imageUrl],
            categoryId: form.category,
            status: true
        };

        patch("/posts/update/", submittedForm)
            .then(data => {
                if (data.status.success) {
                    Alert("success", "Berhasil Mengubah Post");
                    window.location.href = "/settings";
                } else {
                    Alert("error", data.message);
                }
            })
            .catch(error => {
                Alert("error", `Server Error: ${error}`);
            });
    };

    handleInputChange = (e, index = 0) => {
        e.preventDefault();
        const { value, name } = e.target;
        const { formErrors, formValidity, form } = this.state;
        const { validationError, validity } = formValidation(
            name,
            value,
            formValidity,
            formErrors,
            index
        );
        const imgUrlValues = [...form.imageUrl];

        if (name === "imageUrl") {
            imgUrlValues[index] = {
                url: value,
                isThumbnail: false
            };
        }

        this.setState(
            prevState => ({
                form: {
                    ...prevState.form,
                    [name]: name === "imageUrl" ? imgUrlValues : value
                },
                formErrors: validationError,
                formValidity: validity
            }),
            () => {
                this.handleSubmitValidation();
            }
        );
    };

    handleDescriptionInput = value => {
        this.setState(
            prevState => ({
                form: {
                    ...prevState.form,
                    desc: value
                }
            }),
            () => {
                this.handleSubmitValidation();
            }
        );
    };

    handleTabDescriptionChange = tab => {
        this.setState({ tab });
    };

    handleAddimageUrlInput = () => {
        this.setState(
            prevState => ({
                ...prevState,
                form: {
                    ...prevState.form,
                    imageUrl: [...prevState.form.imageUrl, ""]
                },
                formValidity: {
                    ...prevState.formValidity,
                    imageUrl: [...prevState.formValidity.imageUrl, false]
                },
                imageUrlInput: [...prevState.imageUrlInput, ""]
            }),
            () => this.handleSubmitValidation()
        );
    };

    handleRemoveimageUrlClick = index => {
        const { imageUrlInput, form, formValidity, formErrors } = this.state;

        const values = [...imageUrlInput];
        const inputValues = [...form.imageUrl];
        const inputValidity = [...formValidity.imageUrl];
        const inputErros = [...formErrors.imageUrl];

        values.splice(index, 1);
        inputValues.splice(index, 1);
        inputValidity.splice(index, 1);
        inputErros.splice(index, 1);
        this.setState(
            prevState => ({
                ...prevState,
                imageUrlInput: values,
                form: {
                    ...prevState.form,
                    imageUrl: inputValues
                },
                formValidity: {
                    ...prevState.formValidity,
                    imageUrl: inputValidity
                },
                formErrors: {
                    ...prevState.formErrors,
                    imageUrl: inputErros
                }
            }),
            () => this.handleSubmitValidation()
        );
    };

    renderImageUrlInput = () => {
        const { form, formErrors, imageUrlInput } = this.state;
        const { imageUrl } = form;

        return imageUrlInput.map((el, index) => (
            <div className={settingsEditImageWrapper} key={index}>
                <Input
                    type="text"
                    name="imageUrl"
                    id="imageUrl"
                    value={imageUrl[index].url || ""}
                    onChange={e => this.handleInputChange(e, index)}
                    className={errorClass(formErrors.imageUrl[index])}
                    errorMsg={formErrors.imageUrl[index]}
                    placeholder="Your design image url"
                />

                {index > 0 && (
                    <div
                        className={settingsEditRemoveImagesIcon}
                        onClick={e => this.handleRemoveimageUrlClick(index)}
                        role="presentation"
                    >
                        <FontAwesomeIcon icon="times" size="1x" />
                    </div>
                )}

                {index === 0 && (
                    <div
                        className={settingsEditAddImagesIcon}
                        onClick={this.handleAddimageUrlInput}
                        role="presentation"
                    >
                        <FontAwesomeIcon icon="plus" size="1x" />
                    </div>
                )}
            </div>
        ));
    };

    render() {
        const { form, formErrors, categories, tab } = this.state;
        const { title, desc, category } = form;

        return (
            <div className={settingsEditContainer}>
                <Form onSubmit={this.handleFormSubmit}>
                    <p>Title *</p>
                    <Input
                        type="text"
                        name="title"
                        id="title"
                        value={title || ""}
                        onChange={this.handleInputChange}
                        className={errorClass(formErrors.title)}
                        errorMsg={formErrors.title}
                        placeholder="Your design title"
                    />
                    <p>Category *</p>
                    {categories && (
                        <Select
                            options={categories}
                            value={category}
                            name="category"
                            onChange={this.handleInputChange}
                        />
                    )}
                    <p>Description</p>
                    <ReactMde
                        onChange={this.handleDescriptionInput}
                        onTabChange={this.handleTabDescriptionChange}
                        value={desc}
                        className={settingsEditTextEditor}
                        generateMarkdownPreview={markdown => Promise.resolve(this.converter.makeHtml(markdown))
                        }
                        selectedTab={tab}
                    />
                    <p>Image URL *</p>
                    {this.renderImageUrlInput()}
                    <Button type="submit" classBtn="submit">
                        SUBMIT
                    </Button>
                </Form>
            </div>
        );
    }
}

SettingEdit.propTypes = {
    location: objectOf(string).isRequired
};

export default SettingEdit;
