import React from "react";
import { string, func, bool, node } from "prop-types";

import { Button as ButtonStyles, buttonDefault, buttonSuccess, buttonSubmit } from "./styles";

const Button = ({ type, classBtn, children, onClick, disabled }) => {
    let classes = "";
    switch (classBtn) {
        case "submit":
            classes = buttonSubmit;
            break;
        case "success":
            classes = buttonSuccess;
            break;
        default:
            classes = buttonDefault;
            break;
    }
    return (
        <ButtonStyles type={type} className={classes} onClick={onClick} disabled={disabled}>
            {children}
        </ButtonStyles>
    );
};

Button.propTypes = {
    type: string,
    onClick: func,
    disabled: bool,
    classBtn: string,
    children: node.isRequired
};

Button.defaultProps = {
    type: "",
    onClick: null,
    disabled: false,
    classBtn: ""
};

export default Button;
