import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { arrayOf, objectOf, string, any, number, oneOfType } from "prop-types";

import GalleryItem from "./GalleryItem";
import { galleryContainer, galleryWrapper } from "./styles";
import canUseDOM from "../../utils/canUseDOM";

class Gallery extends Component {
    handleItemCick = ({ id, title }) => {
        const { history, match } = this.props;
        const nameURL = title
            .toLowerCase()
            .replace(/ /g, "-")
            .replace(/[^\w-]+/g, "");

        if (canUseDOM) {
            window.scroll({
                top: 0,
                left: 0
            });
        }

        history.push(`${match.path}/${nameURL}?id=${id}`);
    };

    render() {
        const { designs } = this.props;
        return (
            <div className={galleryContainer}>
                {designs.length
                    && designs.map((design, index) => (
                        <div className={galleryWrapper} key={index}>
                            <GalleryItem
                                design={design}
                                onClick={() => this.handleItemCick(design)}
                            />
                        </div>
                    ))}
            </div>
        );
    }
}

Gallery.propTypes = {
    designs: arrayOf(any),
    history: objectOf(oneOfType([string, number, any])).isRequired,
    match: objectOf(any).isRequired
};

Gallery.defaultProps = {
    designs: {}
};

export default withRouter(Gallery);
