import React, { Component, Fragment } from "react";
import { Route } from "react-router-dom";
import Media from "react-media";
import { isBrowser, isMobile } from "react-device-detect";
import { string, objectOf, any } from "prop-types";

import { get } from "../../api";
import LogosDetail from "./LogosDetail";
import Gallery from "../../components/Gallery";
import Alert from "../../components/Alert/index";

class Logos extends Component {
    state = {
        designs: []
    };

    componentDidMount() {
        const apiOptions = JSON.stringify({
            offset: 0,
            limit: 100,
            status: [true, false]
        });

        get(`/posts/get/all/${apiOptions}`)
            .then(data => {
                if (data.status.success) {
                    const metaDesigns = data;

                    const designs = metaDesigns
                        && metaDesigns.data.filter(
                            design => design.category
                                && (design.category.name === "Logo" || design.category.name === "logo")
                        );

                    this.setState({
                        designs
                    });
                } else {
                    Alert("error", "Internal server error");
                }
            })
            .catch(error => {
                console.error("Internal server error: ", error);
                Alert("error", `Internal Server Error: ${error}`);
            });
    }

    render() {
        const { designs } = this.state;
        const { match, location } = this.props;
        return (
            <Fragment>
                <Route path={`${match.path}/:name`} component={LogosDetail} />
                <Media
                    query="(min-width: 501px)"
                    defaultMatches={isBrowser}
                    render={() => <Gallery designs={designs} />}
                />
                {location.pathname === "/logos" && (
                    <Media
                        query="(max-device-width : 480px)"
                        defaultMatches={isMobile}
                        render={() => <Gallery designs={designs} />}
                    />
                )}
            </Fragment>
        );
    }
}

Logos.propTypes = {
    match: objectOf(any).isRequired,
    location: objectOf(string).isRequired
};

export default Logos;
