import { get, isEmpty } from "lodash";
import jwtDecode from "jwt-decode";

import Alert from "../../../components/Alert";
import canUseDOM from "../../../utils/canUseDOM";
import { LOGIN, LOGOUT } from "./actions";

const setInitialState = () => {
    const authData = canUseDOM && JSON.parse(localStorage.getItem("user-auth"));
    const token = get(authData, "token") || null;

    if (token) {
        if (jwtDecode(token).exp < Date.now() / 1000) {
            return false;
        }

        return true;
    }

    return false;
};

export const initialState = {
    loggedIn: setInitialState()
};

const actionHandlers = {
    [LOGIN]: (state, payload) => {
        const { token, exp } = payload;

        if (token && !isEmpty(token) && canUseDOM) {
            localStorage.setItem("user-auth", JSON.stringify({ token, exp }));

            return {
                ...state,
                loggedIn: true
            };
        }
        return { ...state };
    },

    [LOGOUT]: state => {
        if (canUseDOM) {
            localStorage.removeItem("user-auth");
            Alert("success", "Goodbye, Finendi");
            return {
                ...state,
                loggedIn: false
            };
        }

        Alert("error", "Internal Server Error: logout");
        return {
            ...state
        };
    }
};

const authReducer = (state = initialState, { type, payload }) => {
    const hasAction = Object.prototype.hasOwnProperty.call(actionHandlers, type);

    return hasAction ? actionHandlers[type](state, payload) : state;
};

export default authReducer;
