import React, { Component } from "react";

import { post } from "../../api";
import { formValidation, submitValidation, errorClass } from "../../helpers/form-validation";
import { contactContainer, contactTitle, contactInfo, contactForm } from "./styles";
import Form from "../../components/Common/Form";
import Input from "../../components/Common/Input";
import Button from "../../components/Common/Button";
import Alert from "../../components/Alert/index";

class Contact extends Component {
    state = {
        form: {
            fname: "",
            lname: "",
            email: "",
            subject: "",
            message: ""
        },
        formErrors: {
            fname: "",
            lname: "",
            email: "",
            subject: "",
            message: ""
        },
        formValidity: {
            fname: false,
            lname: false,
            email: false,
            subject: false,
            message: false
        },
        isSubmitting: false
    };

    handleSubmit = e => {
        e.preventDefault();
        const { form } = this.state;
        const { fname: firstname, lname: lastname, subject, email, message } = form;

        this.setState({
            isSubmitting: true
        });

        post("/mailer/send/admin", { firstname, lastname, subject, email, message })
            .then(data => {
                if (data.status.success) {
                    Alert("success", "Berhasil mengirimkan pesan");
                    const { formValidity } = this.state;

                    this.setState(
                        () => ({
                            form: {
                                fname: "",
                                lname: "",
                                email: "",
                                subject: "",
                                message: ""
                            },
                            formErrors: {
                                fname: "",
                                lname: "",
                                email: "",
                                subject: "",
                                message: ""
                            },
                            formValidity: {
                                fname: false,
                                lname: false,
                                email: false,
                                subject: false,
                                message: false
                            }
                        }),
                        () => {
                            this.setState({ canSubmit: submitValidation(formValidity, form) });
                        }
                    );
                } else {
                    Alert("error", data.message);
                    console.error(`Error: ${data.message}`);
                }

                this.setState({ isSubmitting: false });
            })
            .catch(error => {
                console.error("Sending mail error: ", error);
                Alert("error", `Server Error: ${error}`);
                this.setState({ isSubmitting: false });
            });
    };

    handleChange = e => {
        e.preventDefault();
        const { value, name } = e.target;
        const { formErrors, formValidity, form } = this.state;
        const { validationError, validity } = formValidation(name, value, formValidity, formErrors);
        this.setState(
            prevState => ({
                form: {
                    ...prevState.form,
                    [name]: value
                },
                formErrors: validationError,
                formValidity: validity
            }),
            () => {
                this.setState({ canSubmit: submitValidation(formValidity, form) });
            }
        );
    };

    render() {
        const { form, formErrors, isSubmitting, canSubmit } = this.state;
        const { fname, lname, subject, email, message } = formErrors;
        return (
            <div className={contactContainer}>
                <div className={contactInfo}>
                    <h2 className={contactTitle}>Contact Me</h2>
                    <p>
                        Email:&nbsp;
                        <a href="mailto:finendi11@gmail.com" target="_top">
                            finendi11@gmail.com
                        </a>
                    </p>
                    <p>
                        You can follow Finendi on{" "}
                        <a href="https://www.instagram.com/finendii/"> Instagram</a>,{" "}
                        <a href="https://twitter.com/finendi">Twitter</a> and{" "}
                        <a href="https://www.facebook.com/finendi"> Facebook</a>.
                    </p>
                </div>
                <div className={contactForm}>
                    <Form onSubmit={this.handleSubmit} className={`${contactForm}Container`}>
                        <p>Name *</p>
                        <div className={`${contactForm}Name`}>
                            <div className={`${contactForm}Fname`}>
                                <label htmlFor="fname">
                                    <Input
                                        type="text"
                                        name="fname"
                                        id="fname"
                                        value={form.fname}
                                        onChange={this.handleChange}
                                        className={errorClass(fname)}
                                        errorMsg={fname}
                                        placeholder="First Name"
                                    />
                                </label>
                            </div>
                            <div className={`${contactForm}Lname`}>
                                <label htmlFor="lname">
                                    <Input
                                        type="text"
                                        name="lname"
                                        id="lname"
                                        value={form.lname}
                                        onChange={this.handleChange}
                                        className={errorClass(lname)}
                                        errorMsg={lname}
                                        placeholder="Last Name"
                                    />
                                </label>
                            </div>
                        </div>
                        <p>Email Address *</p>
                        <Input
                            type="email"
                            name="email"
                            id="email"
                            value={form.email}
                            onChange={this.handleChange}
                            className={errorClass(email)}
                            errorMsg={email}
                            placeholder="yours@email.com"
                        />
                        <p>Subject *</p>
                        <Input
                            type="text"
                            name="subject"
                            id="subject"
                            value={form.subject}
                            onChange={this.handleChange}
                            className={errorClass(subject)}
                            errorMsg={subject}
                            placeholder="Subject"
                        />
                        <p>Message *</p>
                        <Input
                            type="textarea"
                            name="message"
                            id="message"
                            value={form.message}
                            onChange={this.handleChange}
                            className={errorClass(message)}
                            errorMsg={message}
                        />
                        <Button type="submit" classBtn="submit" disabled={!canSubmit}>
                            {isSubmitting ? "SUBMITTING..." : "SUBMIT"}
                        </Button>
                    </Form>
                </div>
            </div>
        );
    }
}

export default Contact;
