import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Backdrop from "../Backdrop";
import { modal, modalHeader, modalCloseButton, modalMain, modalShow } from "./styles";

const Modal = ({ show, onClose, children, title }) => {
    const modalClass = show ? `${modal} ${modalShow}` : `${modal}`;
    return (
        <Fragment>
            <Backdrop show={show} />
            <div className={modalClass}>
                <div className={modalHeader}>
                    <h2>{title}</h2>
                    <div className={modalCloseButton} onClick={onClose} role="presentation">
                        <FontAwesomeIcon icon="times" size="2x" />
                    </div>
                </div>
                <section className={modalMain}>{children}</section>
            </div>
        </Fragment>
    );
};

Modal.propTypes = {
    show: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    children: PropTypes.element.isRequired,
    title: PropTypes.string.isRequired
};

export default Modal;
