import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { func, bool } from "prop-types";

import { handleLogin, handleLogout } from "../../redux/modules/auth";
import Form from "../../components/Common/Form";
import Input from "../../components/Common/Input";
import Button from "../../components/Common/Button";
import { authWrapper, authFooter, authForm } from "./styles";

class Auth extends Component {
    state = {
        username: "",
        password: "",
        isSubmitting: false,
        rememberme: false
    };

    handleLogin = e => {
        e.preventDefault();
        const { onLogin } = this.props;
        const { username, password, rememberme, isSubmitting } = this.state;
        this.setState({
            isSubmitting: !isSubmitting
        });

        onLogin({ username, password, rememberme });
        this.setState({ isSubmitting: false });
    };

    handleLogout = () => {
        const { onLogout } = this.props;
        onLogout();
    };

    handleChangeInput = e => {
        const { name, value } = e.target;

        if (name === "rememberme") {
            const { rememberme } = this.state;

            this.setState({
                rememberme: !rememberme
            });
        } else {
            this.setState({
                [name]: value
            });
        }
    };

    render() {
        const { username, password, rememberme, isSubmitting } = this.state;
        const { auth } = this.props;

        if (auth) {
            return (
                <div className={`${authWrapper}`}>
                    <Button type="submit" classBtn="submit" onClick={this.handleLogout}>
                        Logout
                    </Button>
                    <Link to="/auth/register">
                        <Button type="submit" classBtn="submit">
                            Register
                        </Button>
                    </Link>
                </div>
            );
        }

        return (
            <div className={authWrapper}>
                <Form className={authForm} onSubmit={this.handleLogin} style={{ width: "50%" }}>
                    <label>Username:</label>
                    <Input
                        onChange={this.handleChangeInput}
                        value={username}
                        name="username"
                        type="text"
                        id="text"
                    />
                    <label>Password:</label>
                    <Input
                        onChange={this.handleChangeInput}
                        value={password}
                        name="password"
                        type="password"
                        id="password"
                    />
                    <div className={authFooter}>
                        <label>
                            Remember Me
                            <input
                                type="checkbox"
                                name="rememberme"
                                checked={rememberme}
                                onChange={this.handleChangeInput}
                                style={{ marginLeft: "8px" }}
                            />
                        </label>
                    </div>
                    <Button type="submit" classBtn="submit">
                        {isSubmitting ? "SUBMITTING..." : "SIGN IN"}
                    </Button>
                </Form>
            </div>
        );
    }
}

Auth.propTypes = {
    onLogin: func.isRequired,
    onLogout: func.isRequired,
    auth: bool.isRequired
};

const mapStateToProps = state => ({
    auth: state.auth.loggedIn
});

const mapDispatchToProps = {
    onLogin: handleLogin,
    onLogout: handleLogout
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Auth);
