import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import { get } from "../../api";

import {
    homeContainer,
    homeBackgroundContainer,
    homeBackgroundBackdrop,
    homeTitleWrapper,
    homeTitle,
    homeLogoWrapper,
    homeButtonWrapper,
    homeLogo
} from "./styles";
import Logo from "../../assets/images/logo.png";
import Background from "../../assets/images/background.jpg";
import Button from "../../components/Common/Button";

class Home extends Component {
    state = {
        background: ""
    };

    componentDidMount() {
        this.handleInitBackground();
    }

    handleInitBackground = () => {
        get("/configurations/get/all").then(response => {
            if (
                response.status.success
                && response
                && response.data.length
                && response.data[response.data.length - 1]
            ) {
                this.setState({
                    background: response.data[response.data.length - 1].value
                });
            } else {
                this.setState({
                    background: Background
                });
            }
        });
    };

    render() {
        const { background } = this.state;

        return (
            <div className={homeContainer}>
                <div
                    className={homeBackgroundContainer}
                    style={{ backgroundImage: `url(${background})` }}
                >
                    <div className={homeBackgroundBackdrop} />
                </div>
                <div className={homeTitleWrapper}>
                    <div className={homeLogoWrapper}>
                        <img src={Logo} alt="Finendisgn Logo" className={homeLogo} />
                    </div>
                    <h1 className={homeTitle}>finendisign</h1>
                </div>
                <div className={homeButtonWrapper}>
                    <NavLink to="/illustrations">
                        <Button type="button" classBtn="home">
                            ENTER
                        </Button>
                    </NavLink>
                </div>
            </div>
        );
    }
}
export default Home;
