import React from "react";
import PropTypes from "prop-types";
import { inputContainer, input, inputErrorMsgWrapper, inputErrorMsg } from "./styles";

const Input = ({ type, name, value, onChange, id, className, errorMsg, placeholder }) => {
    if (type === "textarea") {
        return (
            <div className={inputContainer}>
                <textarea
                    value={value}
                    id={id}
                    name={name}
                    onChange={onChange}
                    className={`${input} ${className}`}
                    rows={10}
                    style={{ height: "100px", resize: "vertical" }}
                />
                <div className={inputErrorMsgWrapper}>
                    {errorMsg && <p className={inputErrorMsg}>{errorMsg}</p>}
                </div>
            </div>
        );
    }
    return (
        <div className={inputContainer}>
            <input
                type={type}
                value={value}
                name={name}
                onChange={onChange}
                className={`${input} ${className}`}
                placeholder={placeholder}
            />
            <div className={inputErrorMsgWrapper}>
                {errorMsg && <p className={inputErrorMsg}>{errorMsg}</p>}
            </div>
        </div>
    );
};

Input.propTypes = {
    type: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    value: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
    id: PropTypes.string.isRequired,
    className: PropTypes.string,
    placeholder: PropTypes.string,
    errorMsg: PropTypes.string
};

Input.defaultProps = {
    className: "",
    errorMsg: "",
    placeholder: ""
};

export default Input;
