import React, { Component } from "react";
import { NavLink, withRouter } from "react-router-dom";
import { objectOf, number, oneOfType, string, any } from "prop-types";

import {
    navbarContainer,
    navbarList,
    navbarBrandWrapper,
    navbarMobileContainer,
    navbarMobileButton,
    navbarMobileBar,
    navbarMobileList,
    navbarMobileBrandContainer,
    navbarMobileBrand
} from "./styles";
import NavbarItem from "./NavbarItem";
import ImageLoader from "../ImageLoader";
import Logo from "../../assets/images/logo.png";

class Navbar extends Component {
    state = {
        isNavMobileClicked: false
    };

    handleNavMobileClicked = () => {
        const { isNavMobileClicked } = this.state;
        this.setState({
            isNavMobileClicked: !isNavMobileClicked
        });
    };

    handleBrandClick = () => {
        const { history } = this.props;
        history.push("/");
    };

    render() {
        const { isNavMobileClicked } = this.state;

        return (
            <nav className={navbarContainer}>
                <div className={navbarMobileContainer} role="navigation">
                    <div
                        className={navbarMobileButton}
                        onClick={this.handleNavMobileClicked}
                        role="presentation"
                    >
                        <div className={navbarMobileBar} />
                        <div className={navbarMobileBar} />
                        <div className={navbarMobileBar} />
                    </div>
                    <div
                        className={navbarMobileList}
                        style={{ maxHeight: !isNavMobileClicked ? "0" : "35rem" }}
                    >
                        <NavbarItem />
                    </div>
                    <div className={navbarMobileBrandContainer}>
                        <div className={navbarMobileBrand}>
                            <NavLink to="/">
                                <ImageLoader srcLoaded={Logo} />
                            </NavLink>
                        </div>
                    </div>
                </div>
                <ul className={navbarList}>
                    <li>
                        <div
                            to="/"
                            className={navbarBrandWrapper}
                            onClick={this.handleBrandClick}
                            role="presentation"
                        >
                            <ImageLoader srcLoaded={Logo} />
                        </div>
                    </li>
                    <NavbarItem />
                </ul>
            </nav>
        );
    }
}

Navbar.propTypes = {
    history: objectOf(oneOfType([string, number, any])).isRequired
};

export default withRouter(Navbar);
