import React from "react";
import PropTypes from "prop-types";
import { backdrop, backdropShow, backdropClose } from "./styles";

const Backdrop = ({ show }) => {
    const backdropClass = show ? [backdrop, backdropShow] : [backdrop, backdropClose];
    if (!show) {
        return null;
    }

    return <div className={backdropClass.join(" ")} />;
};

Backdrop.propTypes = {
    show: PropTypes.bool
};

Backdrop.defaultProps = {
    show: false
};

export default Backdrop;
