import jwtDecode from "jwt-decode";

import makeActionCreator from "../../../helpers/redux/makeActionCreator";
import { post } from "../../../api";
import Alert from "../../../components/Alert";

export const LOGIN = "@@auth/LOGIN";
export const LOGOUT = "@@auth/LOGOUT";

const setLogin = makeActionCreator(LOGIN);

export const handleLogin = ({ username, password, remember_me: rememberme }) => dispatch => {
    post("/users/login", { username, password, remember_me: rememberme })
        .then(response => {
            if (response.status.success) {
                const token = response && response.token;
                const decodedToken = jwtDecode(token);

                window.location.href = "/auth";

                dispatch(setLogin({ token, exp: decodedToken.token }));
            } else {
                Alert("error", response.message);
            }
        })
        .catch(error => {
            Alert("error", "Server Error: login");
            throw error;
        });
};

export const handleLogout = makeActionCreator(LOGOUT);
