/* eslint react/no-did-update-set-state: 0*/
import React, { Component } from "react";
import { noop } from "lodash";
import PropTypes from "prop-types";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import ReactMde from "react-mde";
import * as Showdown from "showdown";
import "react-mde/lib/styles/css/react-mde-all.css";
import {
    settingsModalImageWrapper,
    settingsModalAddImagesIcon,
    settingsModalRemoveImagesIcon,
    settingsModalTextEditor
} from "./styles";

import { get as getApi, post } from "../../../api";
import { formValidation, submitValidation, errorClass } from "../../../helpers/form-validation";
import Modal from "../../../components/Modal";
import Button from "../../../components/Common/Button";
import Form from "../../../components/Common/Form";
import Input from "../../../components/Common/Input";
import Select from "../../../components/Common/Select";
import Alert from "../../../components/Alert";

class SettingModal extends Component {
    constructor(props) {
        super(props);

        this.state = {
            imageUrlInput: [""],
            form: {
                title: "",
                category: "1",
                imageUrl: [""],
                desc: ""
            },
            formErrors: {
                title: "",
                category: "",
                imageUrl: [""]
            },
            formValidity: {
                title: false,
                category: false,
                imageUrl: [false]
            },
            tab: "write",
            isSubmitting: false
        };

        this.converter = new Showdown.Converter({
            tables: true,
            simplifiedAutoLink: true,
            strikethrough: true,
            tasklists: true
        });
    }

    componentDidMount() {
        getApi("/categories/get/all").then(data => {
            this.setState({
                categories: data.data
            });
        });
    }

    handleFormSubmit = e => {
        const { form } = this.state;

        const submittedForm = {
            title: form.title,
            description: this.converter.makeHtml(form.desc),
            images: [...form.imageUrl],
            categoryId: form.category,
            status: true
        };

        this.setState({
            isSubmitting: true
        });

        post("/posts/create/", submittedForm)
            .then(data => {
                if (data.status.success) {
                    this.setState({
                        imageUrlInput: [""],
                        form: {
                            title: "",
                            category: "",
                            imageUrl: [""],
                            desc: ""
                        }
                    });

                    Alert("success", "Berhasil Membuat Post Baru");
                    window.location.href = "/settings";
                } else {
                    Alert("error", data.message);
                }

                this.setState({
                    isSubmitting: false
                });
            })
            .catch(error => {
                Alert("error", `Server Error: ${error}`);

                this.setState({
                    isSubmitting: false
                });
            });

        e.preventDefault();
    };

    handleSubmitValidation = () => {
        const { formValidity, form } = this.state;
        this.setState({ canSubmit: submitValidation(form, formValidity) });
    };

    handleInputChange = (e, index = 0) => {
        e.preventDefault();
        const { value, name } = e.target;
        const { formErrors, formValidity, form } = this.state;
        const { validationError, validity } = formValidation(
            name,
            value,
            formValidity,
            formErrors,
            index
        );
        const imgUrlValues = [...form.imageUrl];

        if (name === "imageUrl") {
            imgUrlValues[index] = {
                url: value,
                isThumbnail: false
            };
        }

        this.setState(
            prevState => ({
                form: {
                    ...prevState.form,
                    [name]: name === "imageUrl" ? imgUrlValues : value
                },
                formErrors: validationError,
                formValidity: validity
            }),
            () => {
                this.handleSubmitValidation();
            }
        );
    };

    handleDescriptionInput = value => {
        this.setState(
            prevState => ({
                form: {
                    ...prevState.form,
                    desc: value
                }
            }),
            () => {
                this.handleSubmitValidation();
            }
        );
    };

    handleTabDescriptionChange = tab => {
        this.setState({ tab });
    };

    handleAddimageUrlInput = () => {
        this.setState(
            prevState => ({
                ...prevState,
                form: {
                    ...prevState.form,
                    imageUrl: [...prevState.form.imageUrl, ""]
                },
                formValidity: {
                    ...prevState.formValidity,
                    imageUrl: [...prevState.formValidity.imageUrl, false]
                },
                imageUrlInput: [...prevState.imageUrlInput, ""]
            }),
            () => this.handleSubmitValidation()
        );
    };

    handleRemoveimageUrlClick = index => {
        const { imageUrlInput, form, formValidity, formErrors } = this.state;

        const values = [...imageUrlInput];
        const inputValues = [...form.imageUrl];
        const inputValidity = [...formValidity.imageUrl];
        const inputErros = [...formErrors.imageUrl];

        values.splice(index, 1);
        inputValues.splice(index, 1);
        inputValidity.splice(index, 1);
        inputErros.splice(index, 1);
        this.setState(
            prevState => ({
                ...prevState,
                imageUrlInput: values,
                form: {
                    ...prevState.form,
                    imageUrl: inputValues
                },
                formValidity: {
                    ...prevState.formValidity,
                    imageUrl: inputValidity
                },
                formErrors: {
                    ...prevState.formErrors,
                    imageUrl: inputErros
                }
            }),
            () => this.handleSubmitValidation()
        );
    };

    renderImageUrlInput = () => {
        const { form, formErrors, imageUrlInput } = this.state;
        const { imageUrl } = form;

        return imageUrlInput.map((el, index) => (
            <div className={settingsModalImageWrapper} key={index}>
                <Input
                    type="text"
                    name="imageUrl"
                    id="imageUrl"
                    value={imageUrl[index].url || ""}
                    onChange={e => this.handleInputChange(e, index)}
                    className={errorClass(formErrors.imageUrl[index])}
                    errorMsg={formErrors.imageUrl[index]}
                    placeholder="Your design image url"
                />

                {index > 0 && (
                    <div
                        className={settingsModalRemoveImagesIcon}
                        onClick={e => this.handleRemoveimageUrlClick(index)}
                        role="presentation"
                    >
                        <FontAwesomeIcon icon="times" size="1x" />
                    </div>
                )}

                {index === 0 && (
                    <div
                        className={settingsModalAddImagesIcon}
                        onClick={this.handleAddimageUrlInput}
                        role="presentation"
                    >
                        <FontAwesomeIcon icon="plus" size="1x" />
                    </div>
                )}
            </div>
        ));
    };

    render() {
        const { form, formErrors, canSubmit, categories, tab, isSubmitting } = this.state;
        const { isModalShow, onCloseModal } = this.props;
        const { title, desc, category } = form;

        return (
            <Modal
                show={isModalShow}
                onClose={onCloseModal}
                title={isModalShow ? "New Design" : "Edit Design"}
            >
                <Form onSubmit={this.handleFormSubmit}>
                    <p>Title *</p>
                    <Input
                        type="text"
                        name="title"
                        id="title"
                        value={title || ""}
                        onChange={this.handleInputChange}
                        className={errorClass(formErrors.title)}
                        errorMsg={formErrors.title}
                        placeholder="Your design title"
                    />
                    <p>Category *</p>
                    {categories && (
                        <Select
                            options={categories}
                            value={category}
                            name="category"
                            onChange={this.handleInputChange}
                        />
                    )}
                    <p>Description</p>
                    <ReactMde
                        onChange={this.handleDescriptionInput}
                        onTabChange={this.handleTabDescriptionChange}
                        value={desc}
                        className={settingsModalTextEditor}
                        generateMarkdownPreview={markdown => Promise.resolve(this.converter.makeHtml(markdown))
                        }
                        selectedTab={tab}
                    />
                    <p>Image URL *</p>
                    {this.renderImageUrlInput()}
                    <Button type="submit" classBtn="submit" disabled={!canSubmit}>
                        {isSubmitting ? "SUBMITTING..." : "SUBMIT"}
                    </Button>
                </Form>
            </Modal>
        );
    }
}

SettingModal.propTypes = {
    isModalShow: PropTypes.bool.isRequired,
    onCloseModal: PropTypes.func
};

SettingModal.defaultProps = {
    onCloseModal: noop
};

export default SettingModal;
