import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { objectOf, oneOfType, bool, func } from "prop-types";

import Illustrations from "./views/Illustrations";
import Logos from "./views/Logos";
import Contact from "./views/Contact";
import About from "./views/About";
import Settings from "./views/Settings";
import SettingEdit from "./views/Settings/SettingEdit/index";
import Auth from "./views/Auth";
import Register from "./views/Auth/Register";

const PrivateRoute = ({ component: Component, auth, ...rest }) => (
    <React.Fragment>
        <Route
            {...rest}
            render={props => (auth.auth ? <Component {...props} /> : <Redirect to="/illustrations" />)
            }
        />
    </React.Fragment>
);

const Routes = auth => (
    <Switch>
        <Route path="/about" component={About} />
        <Route path="/contact" component={Contact} />
        <Route path="/illustrations" component={Illustrations} />
        <Route path="/logos" component={Logos} />
        <PrivateRoute path="/settings/edit" auth={auth} component={SettingEdit} />
        <PrivateRoute path="/settings" auth={auth} component={Settings} />
        <PrivateRoute path="/auth/register" auth={auth} component={Register} />
        <Route path="/auth" component={Auth} />
    </Switch>
);

const mapStateToProps = state => ({
    auth: state.auth.loggedIn
});

PrivateRoute.propTypes = {
    component: func.isRequired,
    auth: objectOf(oneOfType([bool, func])).isRequired
};

export default connect(mapStateToProps)(Routes);
