import React, { Component } from "react";
import { get } from "lodash";
import qs from "query-string";
import { objectOf, number, oneOfType, string, bool, any } from "prop-types";

import { get as getApi } from "../../../api";
import GalleryDetailWrapper from "../../../components/Gallery/GalleryDetailWrapper";
import Alert from "../../../components/Alert";

class LogosDetail extends Component {
    state = {
        designs: {}
    };

    componentDidMount() {
        const { location } = this.props;
        const id = parseInt(qs.parse(location.search).id, 10);

        getApi("posts/get", id).then(data => {
            const designs = data;

            this.setState({
                designs
            });
        });
    }

    handleNavigationClick = dir => {
        const { designs } = this.state;
        const { history } = this.props;
        const id = get(designs, "data.id");

        if (id) {
            getApi(`posts/get/${id}/${dir}`).then(data => {
                if (data && data.status.success) {
                    const newDesign = data;

                    history.push(`${newDesign.data.title.toLowerCase()}?id=${newDesign.data.id}`);

                    this.setState({
                        designs: newDesign
                    });
                }

                if (!data.status.success) {
                    Alert(
                        "error",
                        `Post ${dir === "previous" ? "selanjutnya" : "sebelumnya"} tidak ada`
                    );
                }
            });
        }
    };

    handleNextClick = e => {
        e.preventDefault();
        /**
         * Since client want to reverse gallery items order, so for temporary solution we change the direction paramater as well
         * due to api returns data in incorrect order. next -> previous
         * Feel free to re-change if api returns data order correctly
         */
        this.handleNavigationClick("previous");
    };

    handlePrevClick = e => {
        e.preventDefault();
        this.handleNavigationClick("next");
    };

    render() {
        const { designs } = this.state;
        const { match } = this.props;
        return (
            <GalleryDetailWrapper
                designs={designs}
                match={match}
                onClickNext={this.handleNextClick}
                onClickPrev={this.handlePrevClick}
            />
        );
    }
}

LogosDetail.propTypes = {
    location: objectOf(string).isRequired,
    history: objectOf(oneOfType([string, number, any])).isRequired,
    match: objectOf(oneOfType([bool, string, any])).isRequired
};

export default LogosDetail;
