import request from "./request";

export const get = (url = "", id = "") => request({
    url: `${url}/${id}`,
    method: "GET"
});

export const post = (url = "", content) => request({
    url,
    method: "POST",
    data: content
});

export const deleteApi = (url = "", content) => request({
    url,
    method: "DELETE",
    data: content
});

export const patch = (url = "", content) => request({
    url,
    method: "PATCH",
    data: content
});
