import React from "react";
import { func, objectOf, any } from "prop-types";

import ImageLoader from "../../ImageLoader";
import {
    galleryItemContainer,
    galleryItemOverlay,
    galleryItemDetails,
    galleryItemImage
} from "./styles";

const GalleryItem = ({ onClick, design }) => (
    <div className={galleryItemContainer} onClick={onClick} role="presentation">
        <ImageLoader
            srcLoaded={design.images[0] && design.images[0].url}
            className={galleryItemImage}
        />
        <div className={galleryItemOverlay}>
            <div className={galleryItemDetails}>
                <div>{design.title}</div>
                <div>-View-</div>
            </div>
        </div>
    </div>
);

GalleryItem.propTypes = {
    onClick: func.isRequired,
    design: objectOf(any).isRequired
};

export default GalleryItem;
